@charset "UTF-8";
/**
 * z-index function
 * Apply z-index corresponding to the element
 *
 * Possible value (from lowest to highest):
 * background (100) | content (200) | nav (300) | dropdown (400) | modal (500) | loader (600)
 *
 * To change those value go to: /src/styles/variables/_z-index.scss
 *
 * Example:
 * @include z-index('dropdown');
 * => z-index: 300;
 *
 * @include z-index('nav', 3);
 * => z-index: 203;
 */
/**
 * breakpoint mixin
 * Add media-query
 *
 * Example:
 * @include min-width(750px) {
 *   display: none;
 * }
 *   - Result :
 * @media (min-width: 750px) {
 *   @content;
 * }
 */
.ComputeItem {
  padding: 14px 20px 10px;
  background-color: #FFF;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06); }
  .ComputeItem:hover {
    background-color: whitesmoke; }
  .ComputeItem__Operation {
    font-size: 14px;
    color: rgba(0, 0, 0, 0.6); }
  .ComputeItem__Result {
    font-size: 28px; }



