@charset "UTF-8";
/**
 * z-index function
 * Apply z-index corresponding to the element
 *
 * Possible value (from lowest to highest):
 * background (100) | content (200) | nav (300) | dropdown (400) | modal (500) | loader (600)
 *
 * To change those value go to: /src/styles/variables/_z-index.scss
 *
 * Example:
 * @include z-index('dropdown');
 * => z-index: 300;
 *
 * @include z-index('nav', 3);
 * => z-index: 203;
 */
/**
 * breakpoint mixin
 * Add media-query
 *
 * Example:
 * @include min-width(750px) {
 *   display: none;
 * }
 *   - Result :
 * @media (min-width: 750px) {
 *   @content;
 * }
 */
.Button {
  cursor: pointer;
  text-align: center;
  border: rgba(0, 0, 0, 0.07) 1px solid;
  line-height: 46px;
  font-size: 23px; }
  .Button--ComputeType {
    background-color: #FFF; }
    .Button--ComputeType:hover {
      background-color: whitesmoke; }
  .Button--ActionType {
    background-color: #F9BD58;
    color: #FFF; }
    .Button--ActionType:hover {
      background-color: #fac56c; }
  .Button--Disable {
    pointer-events: none;
    cursor: default; }



